.admin-program-content {
  background-color: #ccc;
}

.admin-program-column {
  float: left;
  width: 509px;
  padding: 20px;
}

.admin-program-column h3 {
  margin: 0 0 10px 0;
  padding: 0;
}

.admin-program-box {
  position: relative;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #000;
  color: #fff;
  overflow: hidden;
  border-radius: 3px;
}

/* .admin-program-months {
  height: calc(100vh - 320px);
  overflow: scroll;
} */

.admin-program-months .admin-program-box {
  height: 30px;
  padding: 0;
  line-height: 30px;
  cursor: pointer;
}

.admin-program-months .admin-program-box h3 {
  padding: 0 10px;
  cursor: pointer;
}

.admin-program-months .admin-program-box .admin-program-box-content {
  display: none;
}

.admin-program-box-content {
  display: none;
}

.admin-program-months .admin-program-box-open {
  height: auto;
  min-height: 200px;
  padding: 10px;
  cursor: auto;
}

.admin-program-months .admin-program-box-open .admin-program-box-content {
  display: block;
}

.admin-program-months .admin-program-box-open h3 {
  padding: 0;
  line-height: 10px;
  margin-bottom: 15px;
}

.admin-program-box-toggle {
  position: absolute;
  top: 0;
  right: 10px;
  height: 30px;
  line-height: 30px;
  cursor: pointer;
}

.admin-program-items-new {
  height: 0;
  padding: 0;
  margin: 0;
  font-size: 12px;
  background-color: transparent;
}

.admin-program-items-new-open {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  line-height: 70px;
}

.admin-program-items-new h3 {
  padding-bottom: 10px;
  margin-bottom: 20px;
  border-bottom: 1px solid #666;
}

.admin-program-items-new form > div {
  display: inline-block;
}

.admin-program-items-new label,
.admin-program-items-new input {
  display: inline-block;
  margin: 0 10px;
}

.admin-program-items-new label {
  display: none;
  margin-bottom: 5px;
}

.admin-program-items-new input {
  width: 485px;
  height: 34px;
  margin-bottom: 20px;
  padding-left: 10px;
  font-size: 14px;
  font-weight: bold;
  border: 1px solid #ccc;
}

.admin-program-items-new .custom-select {
  float: none;
  display: inline-block;
  width: 200px;
  height: 38px;
  margin: 0 10px;
}

.admin-program-items-new button {
  height: 38px;
  margin-top: 10px;
  margin-bottom: 5px;
  margin: 0 10px;
  font-size: 14px;
}

.admin-program-devas {
  height: 445px;
  background-color: #000;
  border-radius: 3px;
  margin-bottom: 10px;
}

.admin-program-item {
  display: block;
  position: relative;
  line-height: 20px;
}

.admin-program-item b {
  display: inline-block;
  width: 100px;
  color: #eee;
  background-color: #333;
  border-radius: 3px;
  padding: 3px 5px;
  font-size: 13px;
  margin-right: 15px;
  text-align: center;
}

.admin-program-item::after {
  display: block;
  position: absolute;
  top: 4px;
  right: 5px;
  content: "⚑";
  font-size: 24px;
}

.admin-program-state-invited::after {
  color: purple;
}

.admin-program-state-wish-list::after {
  color: pink;
}

.admin-program-state-needs-permission::after {
  color: red;
}

.admin-program-state-available::after {
  color: green;
}

.admin-program-state-send-invite-soon::after {
  color: yellow;
}

.admin-program-state-needs-production::after {
  color: white;
}
